.graphics {
    width: 100vw;
    //display: flex;

    &_intro {
        width: 100vw;
        height: 200px;
        display: inline-block;
        justify-content: center;
        align-items: center;
        background: repeating-linear-gradient(180deg, #ffb5c0, #ffb5c0, #cfdbff, #d7f1fa, #ffd9b8, #ffb5c0);
        background-size: 400% 400%;
        --mask: linear-gradient(to bottom,
                rgba(0, 0, 0, 1) 0, rgba(0, 0, 0, 1) 70%,
                rgba(0, 0, 0, 0) 95%, rgba(0, 0, 0, 0) 0) 50% 50% / 100% 100% repeat-x;
        -webkit-mask: var(--mask);
        mask: var(--mask);
        z-index: 1;
        overflow: hidden;
        background-repeat: repeat-y;
        -webkit-animation: gradient 8s linear infinite;
        -moz-animation: gradient 8s linear infinite;
        animation: gradient 8s linear infinite;

        &_description {
            width: 100%;
            min-width: 600px;
            line-height: 20px;
            text-align: center;
            position: relative;
            margin-top: -185px;
            color: rgb(36, 34, 36);
            font-family: Andale Mono, monospace;

            &_header {
                position: absolute;
                font-size: 102px;
                margin-top: 30px;
                color: white;
                position: relative;
                opacity: 0.4;
                font-family: Apple Chancery, fantasy;
            }
        }
    }

    &_gallery {
        width: 80vw;
        min-width: 500px;
        display: flex;
        flex-direction: column;
        text-align: left;

        &_container {
            width: 100vw;
            display: flex;

            &_timg {
                width: 30vw;
                margin: 20px 20px 20px 10px;
            }
        }

        &_section {
            width: calc(100% - 200px);
            margin: 100px;
            font-family: 'Roboto Condensed', sans-serif;

            &_header {
                width: 100%;
                margin-top: -30px;

                &_logo {
                    width: 150px;
                    position: absolute;
                    margin: -60px 0 0 -65px;
                    left: 0;
                    opacity: 0.075;
                    z-index: -1;
                }

                &_container {
                    display: flex;
                }

                &_title {
                    font-size: 24px;
                    flex-shrink: 0;
                }

                &_line {
                    width: 80%;
                    height: 1px;
                    flex-grow: 1;
                    margin: 1em 0 0 10px;
                    border-top: 1px solid rgba(0, 0, 0, 0.75);
                }

                &_description {
                    margin-top: -2px;
                    color: rgba(0, 0, 0, 0.5);
                    font-size: 16px;
                    font-style: italic;
                }
            }

            &_graphics {
                width: 100%;
                display: inline;

                &_img {
                    width: max(201px, 16vw);
                    float: center;
                    margin: 10px 20px 20px 40px;
                    transition: 0.5s all ease-in-out;

                    img {
                        -webkit-user-select: none;
                        -khtml-user-select: none;
                        -moz-user-select: none;
                        -o-user-select: none;
                        user-select: none;
                    }

                    &:hover {
                        cursor: pointer;
                        transform: scale(1.1);
                    }
                }

            }
        }
    }

    &_nav {
        width: 20vw;
        min-width: 120px;
        display: flex;
        flex-direction: column;
        align-items: center;
        right: 0;
        position: fixed;
        margin-top: 50px;
        z-index: 1;
        text-align: left;
        font-family: 'Roboto Condensed', sans-serif;
        line-height: 24px;
        transition: 2s all ease-in-out;

        &_link {
            width: 50%;

            &:hover {
                text-decoration: underline;
                text-underline-offset: 5px;
                cursor: pointer;
            }
        }
    }
}

#tropical-night,
#midnight-sun,
#kizuna {
    scroll-margin-top: 300px;
    overflow: initial !important;
}