.back {
    margin-right: 20px;
    float: right;
    margin-bottom: 3vh;

    &:after {
        display: inline-block;
        content: "\00d7";
        font-size: 42px;
        font-weight: 100;
    }

    &:hover {
        cursor: pointer;
    }
}

.layout {
    width: 100vw;
    display: flex;
    justify-content: center;
    font-family: 'Roboto Condensed', sans-serif;
    cursor: default;

    &_img {
        height: 85vh;
        max-width: 100%;
        max-height: 90vh;
        margin-right: 20px;
        margin-bottom: 10px;
        z-index: 1;
        border: 1px solid black;
        padding: 5px;
    }

    &_info {
        width: 25vw;
        min-width: 400px;
        font-size: 20px;
        margin-top: -10px;

        &_header {
            text-align: left;
            border-bottom: 1px solid black;

            &_altname {
                margin-top: -24px;
                color: rgba(0, 0, 0, 0.5);
                font-style: italic;
            }

            &_bottom {
                width: 100%;
                display: inline-flex;
                justify-content: space-between;
                margin: -16px 0 16px 0;
            }
        }

        &_cd {
            width: 55vh;
            height: 55vh;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 0 50px -150px;
            border-radius: 450px;
            background-image:
                conic-gradient(white,
                    #ffb5c0,
                    #cfdbff,
                    white,
                    #d7f1fa,
                    #ffd9b8,
                    white);
            box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
            -webkit-transition: all 0.5s ease-in;
            -webkit-animation-name: rotate;
            -webkit-animation-duration: 20.0s;
            -webkit-animation-iteration-count: infinite;
            -webkit-animation-timing-function: linear;
            transition: all 0.5s ease-in;
            animation-name: rotate;
            animation-duration: 20.0s;
            animation-iteration-count: infinite;
            animation-timing-function: linear;

            &_inner {
                width: 25vh;
                min-width: 170px;
                height: 25vh;
                min-height: 170px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 250px;
                background-color: white;
                box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);

                &_img {
                    width: 100px;
                    opacity: 0.2;
                    animation: none;
                }
            }
        }

        &_quote {
            min-width: 350px;
            position: relative;
            color: rgba(0, 0, 0, 0.5);
            z-index: 1000;
            margin: -50% 0 0 0px;
            padding: 10px 10px 10px 60px;
            background: linear-gradient(to right, transparent, white 40%);
            font-size: 16px;
            font-style: italic;
            text-align: left;
        }
    }
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
        -webkit-transform: rotate(360deg);
    }
}