.about {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;

    &_intro {
        width: 100vw;
        height: 200px;
        display: inline-block;
        justify-content: center;
        align-items: center;
        background: repeating-linear-gradient(180deg, #ffb5c0, #ffb5c0, #cfdbff, #d7f1fa, #ffd9b8, #ffb5c0);
        background-size: 400% 400%;
        --mask: linear-gradient(to bottom,
                rgba(0, 0, 0, 1) 0, rgba(0, 0, 0, 1) 70%,
                rgba(0, 0, 0, 0) 95%, rgba(0, 0, 0, 0) 0) 50% 50% / 100% 100% repeat-x;
        -webkit-mask: var(--mask);
        mask: var(--mask);
        z-index: 1;
        overflow: hidden;
        background-repeat: repeat-y;
        -webkit-animation: gradient 8s linear infinite;
        -moz-animation: gradient 8s linear infinite;
        animation: gradient 8s linear infinite;

        &_description {
            width: 100%;
            min-width: 600px;
            line-height: 20px;
            text-align: center;
            position: relative;
            margin-top: -185px;
            color: rgb(36, 34, 36);
            font-family: Andale Mono, monospace;

            &_header {
                position: absolute;
                font-size: 102px;
                margin-top: 40px;
                color: white;
                position: relative;
                opacity: 0.4;
                font-family: Apple Chancery, fantasy;
            }
        }
    }

    &_content {
        width: 80vw;
        min-width: 500px;
        display: flex;
        flex-direction: column;
        margin-top: 30px;
        text-align: left;
        line-height: 30px;

        &_container {
            width: 100vw;
            min-width: 800px;
            max-width: 1000px;
            display: flex;
            justify-content: center;
        }

        &_header {
            width: 75vw;
            min-width: 800px;
            max-width: 1000px;
            display: inline-flex;
            justify-content: center;
            align-items: center;

            &_intro {
                padding: 40px;

            }

            &_img {
                width: 250px;
                border: 1px solid black;
                padding: 10px;
            }
        }

        &_body {
            min-width: 800px;
            max-width: 1000px;
            margin: 40px;

            li {
                display: inline;
                font-size: 14px;
                margin-left: 10px;
                list-style-type: circle;
            }
        }

        &_end {
            width: 75vw;
            min-width: 800px;
            max-width: 1000px;
            display: inline-flex;
            justify-content: space-between;
            margin: 0;

            &_img {
                width: 250px;
                border: 1px solid black;
                padding: 10px;
                margin: 0px 0px 80px 40px;
                //margin: 0 2vw 100px 2vw;
            }
        }
    }

}