.home {
    width: 100vw;

    &_intro {
        width: 100vw;
        height: 100vh;
        min-height: 500px;
        display: inline-block;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        background: repeating-linear-gradient(180deg, #ffb5c0, #ffb5c0, #cfdbff, #d7f1fa, #ffd9b8, #ffb5c0);
        background-size: 400% 400%;
        --mask: linear-gradient(to bottom,
                rgba(0, 0, 0, 1) 0, rgba(0, 0, 0, 1) 70%,
                rgba(0, 0, 0, 0) 95%, rgba(0, 0, 0, 0) 0) 50% 50% / 100% 100% repeat-x;
        -webkit-mask: var(--mask);
        mask: var(--mask);
        z-index: 1;
        background-repeat: repeat-y;
        -webkit-animation: gradient 8s linear infinite;
        -moz-animation: gradient 8s linear infinite;
        animation: gradient 8s linear infinite;

        &_description {
            //margin: -70px;
            font-size: 20px;
            color: rgba(0, 0, 0, 0.5);

            &_header {
                width: 100%;
                margin-top: 150px;
                font-size: 102px;
                color: white;
                text-align: center;
                font-family: Apple Chancery, fantasy;

                &:after {
                    content: "";
                    display: inline-block;
                    width: 100%;
                }
            }

            &_splash {
                width: 100%;
                height: 100%;
                min-height: 500px;
                margin: 15vw 0 0 -49.9vw;
                z-index: 2;
                position: absolute;
                background-position: 0%;
            }

            &_icons {
                width: 100%;
                display: flex;
                justify-content: center;
                position: fixed;
                margin-top: 25%;
                z-index: 1;
            }

            &_icon {
                width: 70px;
            }

            &_circle {
                width: 500px;
                height: 500px;
                margin: -450px 0 0 -30px;
                background-color: rgba(255, 255, 255, 0.5);
                border-radius: 500px;
                border: 200px solid rgb(255, 255, 255, 0.30);
                -webkit-background-clip: padding-box;
                background-clip: padding-box;
                box-shadow: 0px 0px 0px 300px rgb(255, 255, 255, 0.15);
            }
        }
    }

    &_gallery {
        width: 80vw;
        min-width: 500px;
        display: flex;
        flex-direction: column;
        text-align: left;

        &_container {
            width: 100vw;
            display: inline-block;

            &_top {
                width: 80%;
                height: 100px;
                margin: 100px 10% 100px 10%;
                background-color: #d68585;
            }

            &_section {
                display: flex;
                flex-direction: column;
                align-items: center;
                margin: 50px 0 70px 0;

                &_title {
                    font-size: 32px;
                    font-family: 'Roboto Condensed', sans-serif;
                }

                &_link {
                    width: fit-content;
                    margin-top: -20px;
                    color: #d68585;

                    &:hover {
                        color: #756f75;
                        transition: all 0.2s ease-in-out;
                        cursor: pointer;
                    }
                }
            }

            &_timg {
                width: 300px;
                margin: 20px 20px 20px 10px;
                border-radius: 5px;
            }
        }
    }
}

@keyframes gradient {
    0% {
        background-position: 50% 100%
    }

    100% {
        background-position: 50% -33%
    }
}